header {
    position: relative; }
nav {
    height: 50px;
    background-color: #212121; }
.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;
    &_link {
        text-decoration: none;
        color: #FFF;
        font-size: 15px;
        font-weight: 300;
        line-height: 15px; }
    &_item {
        border-right: 2px dashed #000;
        padding-right: 28px;
        &:last-child {
            border-right: none; } } }
.subheader {
    position: absolute;
    width: 100%;
    height: 87px;
    background-color: rgba(0, 0, 0, 0.6);
    &_logo {
        display: block;
        max-width: 100%;
        margin-top: 16px; }
    &_official {
        margin-top: 6px;
        color: #FFF;
        opacity: 0.5;
        font-size: 13px;
        font-weight: 100; }
    &_call {
        color: #ffffff;
        font-size: 12px;
        font-weight: 300;
        margin-top: 19px;
        text-align: right; }
    &_phone {
        display: block;
        margin-top: 8px;
        color: #ffffff;
        font-size: 22px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: right;
        line-height: 12px;
        text-decoration: none; }
    &_btn {
        text-transform: uppercase;
        width: 172px;
        height: 46px;
        border: none;
        border-radius: 4px;
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
        box-shadow: 0px -3px 0px 0px rgba(0, 0, 0, 0.12) inset;
        margin-top: 21px;
        color: #FFF;
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
        font-size: 14px;
        font-weight: 400;
        @include btn_hover; } }
.promo {
    min-height: 799px;
    background: url("../img/uber_bg.jpg") center center/cover no-repeat;
    padding: 150px 0 135px 0;
    &_header {
        color: #FFF;
        font-size: 60px;
        font-weight: 900;
        text-transform: uppercase;
        line-height: 60px;
        margin: 0;
        text-align: center; }
    &_subheader {
        text-align: center;
        margin-top: 22px;
        color: #FFF;
        font-size: 36px;
        font-weight: 700;
        text-transform: uppercase;
        line-height: 36px;
        margin: 0; }
    &_descr {
        color: #FFF;
        text-align: center;
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        margin-top: 23px; }
    &_btn {
        display: block;
        margin: 118px auto 0 auto;
        width: 259px;
        height: 63px;
        border-radius: 4px;
        border: none;
        background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
        box-shadow: 0px -4px 0px 0px rgba(0, 0, 0, 0.12) inset;
        color: #FFF;
        text-align: center;
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
        font-size: 18px;
        font-weight: 400;
        text-transform: uppercase;
        @include btn_hover; } }

.reasons {
    padding: 71px 0 65px 0;
    &_block {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 35px; }
    &_round {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 26px;
        width: 113px;
        height: 113px;
        background-color: $main-color;
        border-radius: 100%; }

    &_descr {
        width: 343px; }
    &_subtitle {
        line-height: 21px;
        color: $main-color;
        font-size: 17px;
        font-weight: 400; }
    &_text {
        color: #202020;
        font-size: 15px;
        font-weight: 300;
        line-height: 21px; } }


.label {
    position: relative;
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center;
    z-index: 2;
    &_white {
        background-color: #fff;
        color: #000; } }

.title {
    position: relative;
    margin-bottom: 0;
    margin-top: 38px;
    color: #222222;
    font-size: 38px;
    line-height: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    z-index: 2;
    &_white {
        color: #fff; } }

.subtitle {
    position: relative;
    margin-top: 19px;
    color: #222222;
    font-size: 17px;
    font-weight: 400;
    text-align: center;
    z-index: 2;
    &_white {
        color: #fff; } }



.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url('../img/bg/mobile_app.png') center (center / cover) no-repeat;
    .row {
        margin-top: 32px; }
    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;
        &_1 {
            background: url('../icons/mobile_icons/mobile_1.png') center (center / cover) no-repeat; }
        &_2 {
            background: url('../icons/mobile_icons/mobile_2.png') center (center / cover) no-repeat; }
        &_3 {
            background: url('../icons/mobile_icons/mobile_3.png') center (center / cover) no-repeat; }
        &_4 {
            background: url('../icons/mobile_icons/mobile_4.png') center (center / cover) no-repeat; }
        &_5 {
            background: url('../icons/mobile_icons/mobile_5.png') center (center / cover) no-repeat; }
        &_6 {
            background: url('../icons/mobile_icons/mobile_6.png') center (center / cover) no-repeat; }
        &_subtitle {
            color: #ffffff;
            font-size: 24px;
            font-weight: 400;
            line-height: 31px;
            text-transform: uppercase;
            text-align: center; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
            width: 26px;
            height: 26px;
            background-color: #000000;
            margin: 0 auto;
            border-radius: 100%;
            cursor: pointer;
            &::before {
                content: ' ';
                display: block;
                width: 14px;
                height: 2px;
                background-color: $main-color;
                transition: 0.3s all; }

            &::after {
                content: ' ';
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                display: block;
                height: 14px;
                width: 2px;
                background-color: $main-color;
                transition: 0.3s all; }

            &:hover {
                &::before, &::after {
                    background-color: $second-color; } } } } }

.choice {
    position: relative;
    min-height: 895px;
    padding: 57px 0 138px 0;
    background: url('../img/mobile_bg.png') center (center / cover) no-repeat;
    &_img {
        position: relative;
        display: block;
        margin: 56px auto 0 auto;
        z-index: 2; }
    &_descr {
        text-align: center;
        color: #222222;
        font-size: 17px;
        font-weight: 300;
        line-height: 24px; }
    .black {
        position: absolute;
        top: 0;
        left: 0;
        height: 499px;
        width: 100%;
        background-color: #070716;
        z-index: 1; } }

.require {
    padding: 70px 0 90px 0;
    &_block {
        margin: 33px 0; //33 по вертикали и 0 по горизонтали
        &_nmb {
            margin-bottom: 0px; } } //чтобы перебить выше указанный нижний отступ.У блока require уже заданы отступы и чтобы не добавлять еще 33px мы для нижних элементов прописываем margin-botoom 0.(чтобы не было 33 px+ 90px и отспуп не был больше чем нужно)
    &_title {
        color: #1CA8C3;
        font-size: 22px;
        font-weight: 400; }
    &_list {
        list-style-type: none;
        padding-left: 50px;
        margin-top: 22px;
        li {
            position: relative;
            font-style: 17px;
            font-weight: 300;
            line-height: 24px;
            margin-bottom: 9px;
            &::before {
                content: ' ';
                display: block;
                position: absolute;
                left: -32px; //вычисляем расстояние от слова до границы и вычитаем расстояние галочки
                top: 3px;
                width: 21px;
                height: 20px;
                background: url('../icons/require_icons/check_blue.png') center (center/cover) no-repeat; } } }
    &_descr {
        margin-top: 25px;
        font-size: 17px;
        font-weight: 300;
        line-height: 24px;
        color: #1CA8C3; }
    .warning {
        .require_title {
            color: $second-color; }
        .require_list {
            li {
                &::before {
                    background: url('../icons/require_icons/check_red.png'); } } } } }

.world {
    position: relative;
    min-height: 716px;
    padding: 48px 0;
    background: url('../img/map.png') center (center/cover) no-repeat;

    &_link {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 22px;
        text-align: center;
        font-size: 15px;
        font-weight: 400;
        line-height: 68px;
        text-transform: uppercase;
        color: #1fbad6;
        &:hover {
            text-decoration: none;
            color: #fff; } } }

footer {
    padding: 14px 23px 39px 23px;
    a {
        &::hover {
            text-decoration: none; } } }
.footer {
    &_divider {
        width: 299px;
        height: 4px;
        background-color: #1fbad6;
        margin: 0 auto; }

    &_wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding-top: 32px; }
    &_wrapper > div {
        width: 33%;
        &:nth-child(3) { //у третьего элемента этого родителя
            display: flex;
            justify-content: flex-end; } }
    &_social {
        width: 160px;
        display: flex;
        justify-content: space-between;
        &_item {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 4px;
            width: 32px;
            height: 32px;
            background-color: #070716;
            border-radius: 100%;
            text-decoration: none;
            span {
                color: #fff; } } }

    &_links {
        width: 390px;
        margin: 0 auto;
        &_main {
            display: flex;
            justify-content: space-between;
            padding: 0 10px;
            a {
                color: #070716;
                font-size: 15px;
                font-weight: 300;
                text-transform: uppercase;
                text-decoration: none; } }
        &_sub {
            display: flex;
            justify-content: space-between;
            margin-top: 10px;
            a {
                color: #999999;
                font-size: 11px;
                font-weight: 300;
                text-transform: uppercase;
                text-decoration: none; } }
        &_lang {
            display: block;
            text-decoration: none;
            color: #1fbad6;
            font-size: 11px;
            font-weight: 400;
            margin-top: 19px;
            text-transform: uppercase;
            text-align: center; } } }
